/* edit_survey section */

.editSurveySection {
  overflow: hidden;
  .leftContent {
    .projectNameEdit {
      color: #222;
    }
    .section-block {
      .card {
        &-body {
          overflow: hidden;
          .hoverQuestion {
            position: relative;
            &:hover {
              background-color: #f2f5f8;
            }
            .col-1 {
              border-right: 1px solid #ccc;
            }
            .questionBlock {
              .questionHeader {
                display: inline-block;
                padding: 0.1rem 0.3rem;
              }
            }
          }
          & .row:hover .addQuestionControls {
            display: flex;
          }
          .addQuestionControls {
            display: none;
            flex-direction: column;
            i {
              cursor: pointer;
              font-size: 2.2rem;
            }
            .fa-plus-circle {
              color: green;
            }
            .fa-minus-circle {
              color: #cb0c0c;
            }
          }
        }
      }
      .card-text {
        span {
          font-size: 1.2rem;
        }
      }
    }
  }
  .rightControlsColor {
    background-color: #f2f5f8;
    border-left: 1px solid #ccc;
  }
  .rightControls {
    min-height: 100vh;
  }
}

/* edit_survey section end- */