/* radio and checkbox style */
label {
  display: block;
  line-height: 20px;
  font-size: 1.2rem; }

input[type="radio"],
input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  height: 20px;
  width: 20px;
  transition: all 0.15s ease-out 0s;
  background: #cbd1d8;
  border: none;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  margin-right: 0.5rem;
  outline: none;
  position: relative;
  z-index: 1000; }
  input[type="radio"]:hover,
  input[type="checkbox"]:hover {
    background: #9faab7; }
  input[type="radio"]:checked,
  input[type="checkbox"]:checked {
    background: #20e0d0; }
  input[type="radio"]:checked::before,
  input[type="checkbox"]:checked::before {
    content: '✔';
    height: 20px;
    width: 20px;
    display: inline-block;
    position: absolute;
    text-align: center;
    font-size: 15px;
    line-height: 20px; }
  input[type="radio"]:checked::after,
  input[type="checkbox"]:checked::after {
    background: #20e0d0;
    content: '';
    display: block;
    position: relative;
    z-index: 100; }

input[type="radio"] {
  border-radius: 50%; }
  input[type="radio"]::after {
    border-radius: 50%; }

/* radio and checkbox style end---- */
